body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

a {
  text-decoration: none; }

li {
  list-style: none; }

.container {
  max-width: 1100px;
  width: 96%;
  margin: 0 auto; }

.header {
  background-color: #272C34; }
  .header__inner {
    min-height: 10vh;
    display: flex;
    align-items: center;
    justify-content: center; }
  .header__logo {
    color: #fff;
    font-size: 32px; }
    @media (max-width: 520px) {
      .header__logo {
        font-size: 28px; } }

.finder__inner {
  min-height: 80vh;
  padding: 16px; }
  @media (max-width: 520px) {
    .finder__inner {
      padding: 4px; } }

.search-form {
  margin-bottom: 16px; }
  .search-form__title {
    text-align: center;
    font-size: 32px; }
    @media (max-width: 520px) {
      .search-form__title {
        font-size: 26px; } }
  .search-form__subtitle {
    font-size: 18px;
    text-align: center;
    margin-bottom: 8px; }
    @media (max-width: 520px) {
      .search-form__subtitle {
        font-size: 16px;
        margin-bottom: 16px; } }
  .search-form__input {
    max-width: 600px;
    width: 90%;
    display: block;
    margin: auto;
    font-size: 32px;
    padding: 4px;
    border: 1px solid rgba(39, 44, 52, 0.5);
    border-radius: 3px; }
    .search-form__input:focus {
      outline: 0;
      border-color: #05E0B5;
      box-shadow: 0 0 0 2px #05E0B5; }
    @media (max-width: 520px) {
      .search-form__input {
        font-size: 24px;
        padding: 2px; } }

.profile {
  display: flex;
  justify-content: center; }
  @media (max-width: 520px) {
    .profile {
      flex-direction: column;
      align-items: center;
      text-align: center; } }
  .profile__avatar {
    border-radius: 3px;
    border: 1px solid rgba(39, 44, 52, 0.5);
    width: 250px;
    display: block;
    -webkit-transition: -webkit-transform .2s ease;
    transition: -webkit-transform .2s ease;
    transition: transform .2s ease;
    transition: transform .2s ease, -webkit-transform .2s ease;
    margin-right: 16px; }
    @media (max-width: 520px) {
      .profile__avatar {
        width: 150px;
        margin: 0; } }
  .profile__name {
    font-size: 32px;
    font-weight: 600;
    line-height: 1.1; }
    @media (max-width: 520px) {
      .profile__name {
        font-size: 22px; } }
  .profile__login {
    line-height: 1.1;
    font-size: 24px;
    font-weight: 400;
    margin-bottom: 8px; }
    @media (max-width: 520px) {
      .profile__login {
        font-size: 18px;
        margin-bottom: 4px; } }
  .profile__details {
    margin-bottom: 8px; }
  .profile__details-item {
    display: flex;
    align-items: center; }
  .profile__details-icon {
    width: 18px;
    max-height: 18px;
    margin-right: 8px; }
  .profile__details-text {
    margin-top: -3px;
    font-size: 20px;
    font-weight: 400;
    color: #272C34; }
    @media (max-width: 520px) {
      .profile__details-text {
        font-size: 14px; } }
  .profile__link {
    display: inline-block;
    padding: 8px 16px;
    background-color: #272C34;
    color: #fff;
    outline: 0;
    border: 1px solid #272C34;
    border-radius: 3px;
    -webkit-transition: background-color .2s ease;
    transition: background-color .2s ease; }
    .profile__link:focus {
      border-color: #05E0B5;
      box-shadow: 0 0 0 2px #05E0B5; }
    .profile__link:hover {
      background-color: rgba(39, 44, 52, 0.9); }

.footer {
  background-color: #272C34; }
  .footer__inner {
    min-height: 10vh;
    display: flex;
    align-items: center;
    justify-content: center; }
  .footer__made-by {
    color: #fff;
    font-size: 20px; }
    @media (max-width: 520px) {
      .footer__made-by {
        font-size: 18px; } }
  .footer__link {
    color: #05E0B5;
    outline: 0; }
    .footer__link:hover, .footer__link:focus {
      text-decoration: underline; }

